
*{
  margin: 0;
  padding: 0;
}

.bgc {
  background-color: #eee;
}
.box-flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  min-width: 1000px;
}
.left {
  width: 290px;
  background-color: #eee;
}
.right {
  width: 290px;
  background-color: #eee;
}
.content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background-color: #fff;
}
.name {
  margin-top: 50px;
  margin-left: 150px;
  color: #0072ffa6;
  font-size: 40px;
  font-weight: 800;
}
.message {
  margin-left: 150px;
  color: #888888;
  margin-top: 20px;
  font-weight: 900;
}
.Contact_information,
.skill,
.Social,
.project {
  margin-top: 20px;
  margin-left: 150px;
  color: #0072ffa6;
  font-size: 23px;
  font-weight: 800;
}
.information,
.social_mess {
  margin-top: 10px;
  margin-left: 150px;
}
.information_index {
  margin-right: 50px;
  color: #888888;
  font-weight: 900;
}
.social_mess_index {
  margin-right: 200px;
  font-weight: 900;
}
.social_mess_index a {
  text-decoration: none;
  color: #04d9b1;
}
.skill_message {
  margin-top: 20px;
  margin-left: 150px;
  color: #888888;
  font-size: 15px;
  font-weight: 1000;
  list-style: none;
}
.skill_message_index {
  height: 22px;
}
.project_index {
  margin-top: 20px;
  margin-left: 150px;
  color: #888888;
  font-size: 15px;
  font-weight: 1000;
  list-style: none;
}
.project_index_li {
  margin-top: 20px;
}
.project_name {
  margin-left: 550px;
  color: #04d9b1;
}
.project_name a {
  text-decoration: none;
  color: #04d9b1;
}
.project_includes{
  margin-left: 20px;
}
